import Todo from './Todo';

const Todos = ({ todos, toggle, deleteTodo }) => {
    return (
        <div>
            {todos.map(todo => (
                <Todo
                    key={todo.id}
                    todo={todo}
                    toggle={toggle}
                    deleteTodo={deleteTodo}
                />
            ))}
        </div>
    );
};

export default Todos;
