import { Link } from 'react-router-dom';

const Navbar = () => {
    return (
        <nav class='navbar navbar-light bg-secondary'>
            <div class='container-fluid'>
                <Link
                    class='navbar-brand h1 mb-0 text-light'
                    to='/my-first-react-app/'
                >
                    <i class='bi bi-app-indicator me-3'></i>
                    My Todos
                </Link>
                <div class='d-flex'>
                    <Link class='nav-link text-light' to='/my-first-react-app/'>
                        Home
                    </Link>
                    <Link class='nav-link text-light' to='/about'>
                        About
                    </Link>
                </div>
            </div>
        </nav>
    );
};

export default Navbar;
