import { useEffect, useState } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';

import Navbar from './components/Navbar';
import Header from './components/Header';
import Todos from './components/Todos';
import Popper from './components/Popper';
import About from './routes/About';

function App() {
    const [showPopper, setshowPopper] = useState({ state: false, messege: '' });
    const [todos, settodos] = useState([]);

    useEffect(() => {
        const getTodos = async () => {
            const data = await fetchTodos();
            settodos(data);
        };

        getTodos();
    }, []);

    const fetchTodos = async () => {
        const res = await fetch('https://jsonplaceholder.typicode.com/todos');

        const data = await res.json();
        return data;
    };

    //delete todo
    const deleteTodo = id => {
        setshowPopper({ state: true, messege: 'Deleting Todo' });
        const deleteTodoCloud = async () => {
            const res = await fetch(
                `https://jsonplaceholder.typicode.com/todos/${id}`,
                {
                    method: 'DELETE',
                }
            );
            console.log(res);
            settodos(todos.filter(todo => todo.id !== id));

            setshowPopper({ state: false, messege: '' });
        };

        deleteTodoCloud();
    };

    //toggle completed
    const toggleComplete = id => {
        setshowPopper({ state: true, messege: 'Updating toggle' });
        settodos(
            todos.map(todo =>
                todo.id === id ? { ...todo, completed: !todo.completed } : todo
            )
        );
        const toggleCloud = async id => {
            const res = await fetch(
                `https://jsonplaceholder.typicode.com/todos/${id}`,
                {
                    method: 'PATCH',
                    body: JSON.stringify({
                        completed: !todos[id - 1].completed,
                    }),
                    headers: {
                        'Content-type': 'application/json; charset=UTF-8',
                    },
                }
            );
            const data = await res.json();
            setshowPopper({ state: false, messege: '' });
        };

        toggleCloud(id);
    };

    //post todo
    const saveTodo = newTodo => {
        setshowPopper({ state: true, messege: 'Saving Todo' });
        const postTodo = async () => {
            const res = await fetch(
                `https://jsonplaceholder.typicode.com/todos`,
                {
                    method: 'POST',
                    body: JSON.stringify({
                        newTodo,
                    }),
                    headers: {
                        'Content-type': 'application/json; charset=UTF-8',
                    },
                }
            );

            const data = await res.json();
            console.log(data);
            settodos([data.newTodo, ...todos]);
            setshowPopper({ state: false, messege: '' });
        };

        postTodo();
    };

    return (
        <Router>
            <div className='App'>
                <Navbar />
                <div class='d-flex justify-content-center py-5'>
                    <Route
                        path='/my-first-react-app/'
                        exact
                        render={() => (
                            <div class='card' style={header_style}>
                                <Header postTodo={saveTodo} />
                                <div
                                    class='card-body bg-light'
                                    style={{
                                        minHeight: '200px',
                                        maxHeight: '40vh',
                                        overflowY: 'scroll',
                                    }}
                                >
                                    <Todos
                                        todos={todos}
                                        toggle={toggleComplete}
                                        deleteTodo={deleteTodo}
                                    />
                                </div>
                            </div>
                        )}
                    ></Route>
                    <Route path='/about' component={About}></Route>
                </div>
                '
                {showPopper.state ? (
                    <Popper text={showPopper.messege} />
                ) : (
                    <></>
                )}
            </div>
        </Router>
    );
}

const header_style = {
    width: '60vw',
    minWidth: '500px',
    maxWidth: '650px',
    minHeight: '200px',
};

export default App;
